// @flow

import React from "react"
import { PrivacyPolicy, CookiePolicy } from "@corgi/gatsby-plugin-iubenda"
import { Box, Flex } from "grid-styled"
import { Text } from "components/atoms/typography"
import Container from "components/atoms/Container"
import Hr from "components/atoms/Hr"
import Logo from "components/atoms/Logo"
import SocialIcons from "components/molecules/SocialIcons"

type Props = {
  description: string,
}

export default function Footer({ description, privacyPolicyId }: Props) {
  return (
    <footer>
      <Container pt={5} p={3}>
        <Hr />
        <a href="https://www.polidea.com" target="_blank" rel="noopener">
          <Logo mx="auto" my={[4, 5]} />
        </a>
        <Text pb={4}>{description}</Text>
        <SocialIcons />
        <Flex>
          <Box pr={2}>
            <PrivacyPolicy privacyPolicyId={privacyPolicyId} />
          </Box>
          <Box pr={2}>
            <CookiePolicy privacyPolicyId={privacyPolicyId} />
          </Box>
        </Flex>
      </Container>
    </footer>
  )
}



